.nav-item{
	display: inline-block;
	height: 100%;
	width: 70px;
}
.nav-item:last-child {
  float: right;
}
.nav-button {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	text-align: center;
	line-height: 40px;
	margin: 5px;
	-webkit-transition: 0.2s ease;
	-o-transition: 0.2s ease;
	transition: 0.2s ease;
	font-size: 12px;
	font-weight: 600;
}

.nav-button:hover {
  border-bottom: 0.2px solid #ddd;
  -webkit-transition: .5 ease;
  -o-transition: .5 ease;
  transition: .5 ease;
}




