.choice-popover-trigger__button {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
 	width:40px;
 	height: 40px;
	color:grey;
	border-bottom: 1px solid white;
	text-align: center;
	line-height: 40px;
	font-size: 2em;
}

.choice-popover-trigger__button:hover {
	color:#3CB371;
	-webkit-transition: 0.1s ease;
	-o-transition: 0.1s ease;
	transition: 0.1s ease;
}


