.popover {
  position: relative;
}

.popover__trigger {
  outline: 0;
  display: inline-block;
}

.popover__trigger :hover {
  cursor: pointer;
  text-decoration: none;
}

.popover__panel {
  z-index: 501;
  display: inline-block;
}







