.quiz {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	margin: 0 auto;
}

.quiz__intro {
	padding:  100px 50px 70px 50px;
	display: table;
	background-color: #000000;
}

.quiz__intro__image {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: table-cell;
	width: 35%;
}

.quiz__intro__text {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: table-cell;
	width: 65%;
	height: 100%;
	font-size: 35px;
	vertical-align: middle;
	font-weight: 700;
	padding: 20px;
	text-align: center;
	color: #fff;
}

.quiz__intro__logo {
	width: 100%;
}

.quiz__body {
	margin: 0 auto;
	width: 80%;
	padding: 50px 0 50px 0;
}


