.quiz-taker {
  	margin: 0 auto;

}


.quiz-taker__title{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 100%;
  	margin-bottom: 20px;
  	padding: 10px;
	background: #FFFFFF;
	text-align: center;
	-webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
	        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
	border-radius: 3px;
	font-weight: 600;
	font-size: 2em;
}

.quiz-taker__body{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 100%;
  	margin-bottom: 20px;
  	padding: 10px;
	background: #FFFFFF;
	text-align: center;
	-webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
	        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
	border-radius: 3px;
}

.quiz-taker__problems-count {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	font-size: 20px;
	font-weight: 1000;
	width: 30px;
	height: 30px;
	border-radius: 3px;
	background-color: #1ea04e;
	color: white;
}

.quiz-taker__question {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding: 10px;
	margin: 10px;
	margin: 0 auto;
	margin-bottom: 20px;
	font-size: 1.5em;
	font-weight: 600;
	text-align: left;
	border: none;
	color: #1ea04e;
}

.quiz-taker__choices {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 80%;
	margin: 0 auto;
	margin-bottom: 20px;
	text-align: left;
	line-height: 40px;
}

.quiz-taker__navigation {
	width: 100%;
	height: 60px;
}


.quiz-taker__button.submit {
	display: block;
	margin: 0 auto;
	width: 200px;
	height: 60px;
	font-weight: 1000;
	font-size: 2em;
	text-align: center;
	color: white;
	background-color: #00aced; /*twitter*/
}



